* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

img {
  max-width: 100%;
  width: 100%;
  height: 220px;
  display: block;
}

.container {
  width: 1400px;
  margin: auto;
}

.search {
  background: #00f;
  margin-bottom: 24px;
  padding: 15px 0;
}

form {
  justify-content: center;
  gap: 15px;
  display: flex;
}

form button {
  cursor: pointer;
  background: #fafafa;
  border: none;
  border-radius: 5px;
  padding: 5px 15px;
  font-size: 16px;
  font-weight: 600;
  transition: all .25s;
}

form button:hover {
  color: #fafafa;
  background: green;
}

form input {
  width: 500px;
  border: none;
  border-radius: 5px;
}

.photo-card {
  width: calc(25% - 20px);
  border: 1px solid #000;
}

.img {
  width: 100%;
  padding-bottom: 65%;
  position: relative;
}

.info {
  display: flex;
}

.info-item {
  width: 25%;
  text-align: center;
  border-right: 1px solid #000;
  margin: 0;
  padding: 15px 5px 5px;
}

.info-item:last-child {
  border-right: unset;
}

.info-item b {
  text-align: center;
  font-size: 12px;
  font-weight: 900;
  line-height: 1.2;
  display: block;
}

.text {
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
  display: block;
}

.pagenum {
  justify-content: center;
  padding: 20px 0;
  display: flex;
}

.pagenum button {
  color: #fff;
  cursor: pointer;
  background: #639;
  border: none;
  border-radius: 5px;
  padding: 7px 15px;
  font-size: 16px;
  font-weight: 700;
  transition: all .25s;
}

.pagenum button:hover {
  background: #8a2be2;
}

.display-none {
  display: none;
}

